<template>
  <JitsiMeeting
    :user-info="userInfo"
    :domain="domain"
    :room-name="roomName"
    height="100%"
    width="100%"
    :devices="devices"
    :config-overwrite="configOverwrite"
    @on-ready-to-close="readyToClose"
    @on-api-ready="(externalApi: IJitsiMeetExternalApi) => handleApiReady(externalApi)"
  >
  </JitsiMeeting>
</template>

<script setup lang="ts">
import { useMediaDeviceStore, useUserStore } from '@/stores';
import { onBeforeMount, ref } from 'vue';
import { useRoute } from 'vue-router';
import type IJitsiMeetExternalApi from '@jitsi/vue-sdk/dist/types/src/types/IJitsiMeetExternalApi';

const mediaDeviceStore = useMediaDeviceStore();
const userStore = useUserStore();
const route = useRoute();

const roomName = ref('');
const apiRef = ref();

const devices = {
  audioInput: mediaDeviceStore?.currentMicrophone?.label,
  audioOutput: mediaDeviceStore?.currentSpeaker?.label,
  videoInput: mediaDeviceStore?.currentCamera?.label,
};
const domain = import.meta.env.VITE_CONFERENCE;
const userInfo = {
  displayName: userStore.user.userName,
};

const configOverwrite = {
  prejoinConfig: {
    enabled: false,
  },
};

onBeforeMount(() => {
  roomName.value = route.query.room as string;
});

function readyToClose() {
  window.close();
}

function handleApiReady(externalApi: IJitsiMeetExternalApi) {
  apiRef.value = externalApi;
}
</script>

<style>
#app {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100%;
}

#app > div {
  overflow: hidden;
  height: 100%;
}
</style>
